import {ErrorMessage, Field, Form, Formik} from "formik";
import React, {useEffect, useState} from "react";
import {getAllManufacture} from "../services/manufactureService";
import {useNavigate} from "react-router-dom";
import {addNewProduct, getProductById} from "../services/productService";
import {toast} from "react-toastify";
import * as Yup from "yup";
import {useParams} from "react-router-dom";
import {Link} from "react-router";

export  default function EditComponent(){
    const [manufactureList, setManufactureList] = useState([]);
    const {id} = useParams()
    const [product, setProduct] = useState(null);
    useEffect(() => {
        const fetchData = async () => {
            const list = await getAllManufacture();
            setManufactureList(list);
        }
        fetchData();
    });
    useEffect(()=>{
        const fetchProduct = async () => {
            let product = await getProductById(id);
            product ={
                ...product,
                manufacture: JSON.stringify(product.manufacture)
            }
            setProduct(product);
            console.log(product)
        }
        fetchProduct();
    },[])

    const navigate = useNavigate();
    const handleSubmit = async (value) => {
        console.log(value);
        const product = {
            ...value,
            manufacture: JSON.parse(value.manufacture)
        }
        await addNewProduct(product);
        toast.success("Thêm mới thành công");

        navigate('/products');
    }
    const handleValidate = Yup.object({
        // id: Yup.number().required(" Id không được để trống")
        //     .min(1,"Phải là số nguyên dương"),
        name: Yup.string().required("Tên không được để trống")
            .matches(/^[A-Z][a-z\d]*(\s[A-Z][a-z\d]*)*$/, 'Tên chưa đúng định dạng'),
        manufacture: Yup.string().required("Phải chọn"),
        feature: Yup.array().min(1, "Phải chọn ít nhất 1")
    })
    if (!product){
        return "....isLoading"
    }
    return (
        <div className="w-50" style={{marginLeft: '400px'}}>
            <h3 className="text-center"> Chỉnh sửa sản phẩm</h3>
            <Formik initialValues={product} onSubmit={handleSubmit} validationSchema={handleValidate}>
                <Form className="container mt-4">
                    {/* Name Field */}
                    <div className="mb-3 row">
                        <label className="col-sm-3 col-form-label">Name:</label>
                        <div className="col-sm-9">
                            <Field
                                type="text"
                                name="name"
                                className="form-control form-control-sm"
                            />
                            <ErrorMessage
                                name="name"
                                component="small"
                                className="text-danger mt-1 d-block"
                            />
                        </div>
                    </div>

                    {/* Number of SIM */}
                    <div className="mb-3 row">
                        <label className="col-sm-3 col-form-label">Number of SIM:</label>
                        <div className="col-sm-9 d-flex align-items-center gap-3">
                            <label>
                                <Field type="radio" name="sim" value="1" className="form-check-input" />
                                1 SIM
                            </label>
                            <label>
                                <Field type="radio" name="sim" value="2" className="form-check-input" />
                                2 SIM
                            </label>
                        </div>
                        <ErrorMessage
                            name="sim"
                            component="small"
                            className="text-danger mt-1 d-block offset-sm-3"
                        />
                    </div>

                    {/* Feature */}
                    <div className="mb-3 row">
                        <label className="col-sm-3 col-form-label">Feature:</label>
                        <div className="col-sm-9 d-flex align-items-center gap-3">
                            <label>
                                <Field type="checkbox" name="feature" value="Bluetooth" className="form-check-input" />
                                Bluetooth
                            </label>
                            <label>
                                <Field type="checkbox" name="feature" value="5G" className="form-check-input" />
                                5G
                            </label>
                            <label>
                                <Field type="checkbox" name="feature" value="Camera" className="form-check-input" />
                                Camera
                            </label>
                        </div>
                        <ErrorMessage
                            name="feature"
                            component="small"
                            className="text-danger mt-1 d-block offset-sm-3"
                        />
                    </div>

                    {/* Manufacturer */}
                    <div className="mb-3 row">
                        <label className="col-sm-3 col-form-label">Manufacturer:</label>
                        <div className="col-sm-9">
                            <Field
                                as="select"
                                name="manufacture"
                                className="form-select form-select-sm"
                            >
                                <option value={""}>-------Select----------</option>
                                {manufactureList.map((m) => (
                                    <option key={m.id} value={JSON.stringify(m)}>
                                        {m.name}
                                    </option>
                                ))}
                            </Field>
                            <ErrorMessage
                                name="manufacture"
                                component="small"
                                className="text-danger mt-1 d-block"
                            />
                        </div>
                    </div>
                    <div className="text-center">
                        <button type="submit" className="btn btn-primary btn-sm">
                            Save
                        </button>
                        <Link to={'/products'} type="submit" className="btn btn-secondary btn-sm">
                            Cancel
                        </Link>
                    </div>
                </Form>
            </Formik>

        </div>
    )
}