
import axios from "axios";
let url =`https://back-end-c07.onrender.com/products`
export async  function getAllProduct(page) {
    try {
        const  response = await axios.get(`${url}?_sort=name&_order=asc&_page=${page}&_limit=2`);
        console.log(response);
        return {data: response.data,totalCount:response.headers['x-total-count']};

    }catch (e) {
        console.log("lỗi "+e);
        return [];
    }
}

export async  function searchProductByName(searchName,manufactureId,page) {

    let url1 =`${url}?name_like=${searchName}&manufacture.id=${manufactureId}&_sort=name&_order=asc&_page=${page}&_limit=2`
    if (manufactureId==""){
        url1=`${url}?name_like=${searchName}&_sort=name&_order=asc&_page=${page}&_limit=2`
    }
    try {
        const  response = await axios.get(url1);
        console.log("-------search--------")
        console.log(response.data);
        return response.data;
    }catch (e) {
        console.log("lỗi "+e);
        return [];
    }
}


export async function addNewProduct(product) {

    try {
        const  response = await axios.post(url,product);
        console.log("---------service- thêm mới-------------")
    }catch (e) {
        console.log("lỗi "+e);
    }

}

export async function getProductById(id) {
    try {
        const  response = await axios.get(`${url}/`+id);
        console.log(response);
        return response.data;

    }catch (e) {
        console.log("lỗi "+e);
        return null;
    }
}
export async function deleteProductById(id) {
    try {
        const  response = await axios.delete(`${url}/`+id);
        console.log("---------service- thêm mới-------------")
    }catch (e) {
        console.log("lỗi "+e);
    }
}
